<template>
    <div class="transfer-log-view">
        <div class="py-1 w-100 text-start"><b-button @click="HandleRefreshClick" size="sm" variant="primary"></b-button></div>
        <el-table v-loading="loading" :data="transfer_data" border style="height: 600px;">
            <el-table-column label="" prop="StartTime"></el-table-column>
            <el-table-column label="" prop="EndTime"></el-table-column>
            <el-table-column label="()" prop="TimeSpend"></el-table-column>
            <el-table-column label="" prop="FromName"></el-table-column>
            <el-table-column label="" prop="ToName"></el-table-column>
            <el-table-column label="" prop="StartLocName"></el-table-column>
            <el-table-column label="(%)" prop="BatLvStart"></el-table-column>
            <el-table-column label="(%)" prop="BatLvEnd"></el-table-column>
            <el-table-column label="(%)" prop="BatLoss"></el-table-column>
            <el-table-column label="(m)" prop="Odometry">
                <template #default="scope"> {{ (scope.row.Odometry * 1000).toFixed(1) }} </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import { LogAPI } from '@/api/VMSAPI.js'
export default {
    data() {
        return {
            transfer_data: [],
            loading: false,
        }
    },
    methods: {
        async FetchTodayTransferData() {
            this.loading = true;
            this.transfer_data = [];
            this.transfer_data = await LogAPI.GetTransferLogToday();
            this.loading = false
        },
        HandleRefreshClick() {
            this.FetchTodayTransferData()
        }
    },
    mounted() {
        this.FetchTodayTransferData();
    }
}
</script>

<style></style>